
html {
  font-size   : $mainFontSize;
  line-height : $mainLineHeight;
  overflow-x  : hidden;
  width       : 100%;

  max-width   : 100vw;
  height      : 100%;
  color       : #000;

}

body {
  font-family             : $mainFontFamily;
  font-size               : $mainFontSize;
  font-weight             : 400;
  line-height             : $mainLineHeight;
  overflow-x              : hidden;
  width                   : 100%;
  max-width               : 100vw;
  margin                  : 0;
  padding                 : 0;
  color                   : #000;
  background-color        : #fff;
  -webkit-font-smoothing  : antialiased;
  -moz-osx-font-smoothing : grayscale;
}

ul,
ol,
li,
p {
  font-size     : $mainFontSize;
  line-height   : $mainLineHeight;
  margin-top    : 1em;
  margin-bottom : 1em;
}


button {
  cursor : pointer;
}

a {
  text-decoration          : underline;
  text-decoration-skip-ink : none;

  &:active,
  &:focus,
  &:hover {

  }
}


.container {
  position  : relative;
  flex      : 0 0 auto;
  width     : 100%;
  max-width : 1100px;
  margin    : 0 auto;
  padding   : 0 15px;


}


.page {
  display        : flex;
  flex-direction : column;
  min-height     : 100vh;

  &__header {
  }

  &__footer {
    margin-top : auto;
  }
}


.swiper-container {
  position     : relative;
  z-index      : 1;
  margin-right : auto;
  margin-left  : auto;
  padding      : 0;
  list-style   : none
}

.swiper-wrapper {
  position            : relative;
  z-index             : 1;
  display             : flex;
  box-sizing          : content-box;
  width               : 100%;
  height              : 100%;
  transition-property : -webkit-transform;
  transition-property : transform;
  transition-property : transform, -webkit-transform
}

.swiper-slide {
  position            : relative;
  flex-shrink         : 0;
  width               : 100%;
  height              : 100%;
  transition-property : -webkit-transform;
  transition-property : transform;
  transition-property : transform, -webkit-transform
}

.swiper-pagination {
  position        : absolute;
  bottom          : -32px;
  display         : flex;
  justify-content : center;
  width           : 100%;
  text-align      : center
}

.swiper-pagination-bullet {
  display          : inline-block;
  width            : 12px;
  height           : 12px;
  margin           : 0 12px;
  vertical-align   : bottom;
  border-radius    : 50%;
  background-color : #ced0d1
}

.swiper-pagination-bullet-active {
  background-color : #36393a
}


.block {
  // .block__title
  &__title {
    font-size   : 30px;
    font-weight : 800;
    line-height : 1.2333;
    position    : relative;
    left        : 50%;
    display     : inline-block;
    margin      : 35px auto;
    padding     : 0 10px;
    transform   : translateX(-50%);
    color       : #161616;

    &::before {
      content          : '';
      display          : block;
      width            : 50px;
      height           : 39px;
      background-color : #feb618;
      position : absolute;
      z-index : -1;
      top : -2px;
      left : 0
    }
  }
}
